@import url('https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap');

*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

html, body, :root{
    height: 100%;
}

body{
    background: #ffffff;
    -webkit-font-smoothing: antialiased;
    margin-top: 1%;
}

body, input, button{
    font-family: Roboto, sans-serif;
}